}
@media (max-width: 1199px) {
  .contacts__item {
    row-gap: 15px;
  }
}
@media (hover: hover) {
  .contacts__item:hover:not(.focus-visible) a {
    color: rgba(209, 10, 66, 0.8);
    transition: color 0.3s ease;
  }
}
.contacts__item.focus-visible:focus a {
  color: rgba(209, 10, 66, 0.8);
  transition: color 0.3s ease;
}

.contacts__name {
  margin: 0;
  font-weight: 700;
  font-size: 16px;
  line-height: 1;
}
