  font-size: 14px;
  line-height: 1;
}
@media (max-width: 1199px) {
  .contacts__description {
    font-size: 18px;
  }
}

.footer {
  background-color: #030f1a;
}

.footer__inner {
  padding-block: 42px 33px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
@media (max-width: 1199px) {
  .footer__inner {
    padding-block: 45px 41px;
    padding-inline: 45px 39px;
    gap: 30px;
  }
}
@media (max-width: 767px) {
  .footer__inner {
    flex-direction: column;
    padding-inline: 15px;
    padding-block: 47px 52px;
  }
}

.footer__logo {
  cursor: pointer;
  height: max-content;
  padding-block: 8px;
  display: inline-block;
}
@media (max-width: 1199px) {
  .footer__logo {
    padding-block: 3px 8px;
    max-width: 81px;
  }
  .footer__logo img {
    min-width: 100%;
  }
}
@media (max-width: 767px) {
  .footer__logo {
    position: static;
    max-width: 68px;
    padding-block: 4px;
  }
  .footer__logo img {
    min-width: 68px;
    min-height: 15px;
  }
}
@media (hover: hover) {
  .footer__logo:hover:not(.focus-visible) {
    opacity: 0.65;
    transition: opacity 0.3s ease;
  }
}
.footer__logo.focus-visible:focus {
  opacity: 0.65;
  transition: opacity 0.3s ease;
}

.footer__nav-list {
  margin: 0 0 0 9px;
  padding: 0;
  list-style: none;
  max-width: max-content;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(5, max-content);
  column-gap: 40px;
  row-gap: 10px;
}
@media (max-width: 1199px) {
  .footer__nav-list {
    gap: 30px;
  }
}
@media (max-width: 767px) {
  .footer__nav-list {
    gap: 75px;
    display: block;
    column-count: 2;
    padding: 0;
    margin-inline: 0;
    margin-top: -54px;
  }
}

.footer__nav-item {
  color: #ffffff;
}
@media (max-width: 767px) {
  .footer__nav-item {
    margin-top: 16px;
  }
  .footer__nav-item:first-child {
    margin-top: 40px;
  }
}

.footer__link {
  color: inherit;
  font-size: 14px;
  line-height: 1;
}
@media (max-width: 767px) {
  .footer__link {
    font-size: 16px;
  }
}
@media (hover: hover) {
  .footer__link:hover:not(.focus-visible) {
    color: #d10a42;
    transition: color 0.3s ease;
  }
}
.footer__link.focus-visible:focus {
  color: #d10a42;
  transition: color 0.3s ease;
}

.footer__socials {
  margin: 0;
}
@media (max-width: 767px) {
  .footer__socials {
    margin: -5px 0 0;
  }
}

.socials {
  padding: 0;
  list-style: none;
  max-width: 97px;
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 11px;
}
@media (max-width: 767px) {
  .socials {
    min-width: 100%;
  }
}

.socials__item {
  display: inline-block;
  max-width: 25px;
  width: 100%;
  min-width: 25px;
  max-height: 25px;
  height: 100%;
  min-height: 25px;
}

.socials__link {
  width: 25px;
  height: 25px;
  background-color: #ffffff;
  color: #030f1a;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (hover: hover) {
  .socials__link:hover:not(.focus-visible) {
    background-color: #d10a42;
    color: #ffffff;
    transition: all 0.3s ease;
  }
}
.socials__link.focus-visible:focus {
  background-color: #d10a42;
  color: #ffffff;
  transition: all 0.3s ease;
}

