// Colors
// ---------------------------------

// Default
$color-dark-blue:                   #102654;
$color-desaturated-dark-blue:       #495a7d;
$color-secondary-blue:              #1c3374;
$color-light-blue:                  #103aae;
$color-opacity-blue:                rgba(20, 54, 122, 0.5);
$color-purple:                      #00359f;
$color-blue-purple:                 #2e35ac;
$color-spindle:                     #b6bdcc;
$color-grey:                        #f8f9fc;
$color-light-slate-grey:            #74819c;
$color-secondary-grey:              #e8e8e8;
$color-light-grey:                  rgba(120, 120, 120, 0.2);
$color-red:                         #d10a42;
$color-saturated-red:               #ed0233;
$color-default-black:               #000000;
$color-black-russian:               #030f1a;
$color-default-white:               #ffffff;
$color-transparent:                 rgba(255, 255, 255, 0);

// Project palette
//     https://www.htmlcsscolor.com/hex/334482

$color-neon-blue:                   #2c39f2;
$color-torch-red:                   #ff1553;

// Gradient
$red-to-blue-to-right:              linear-gradient(97.05deg, #ed0233 17.6%, #003eb7 95.31%);
$black-to-right:                    linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0) 75%);

// Typography
// ---------------------------------

//    html   
$fz-default:                        16px;

//     ios     
// https://www.artlebedev.ru/kovodstvo/sections/159/#13
$ff-roboto-condensed:               "Roboto Condensed", "Arial", sans-serif;
$ff-alumi-sans:                     "Alumni Sans", "Arial", sans-serif;
$ff-placeholder:                    "Placeholder", "Arial", sans-serif;

// Animation
// ---------------------------------

$tf-default:                        ease;
$trans-default:                     0.3s $tf-default;

//      $tf-default,     
$trans-modal:                       0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600:                         0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-320:                            320px;
$vp-375:                            375px;
$vp-768:                            768px;
$vp-1024:                           1024px;
$vp-1200:                           1200px;
$vp-1440:                           1440px;
$vp-1920:                           1920px;

// Retina
// ---------------------------------

$retina-dpi:                        144dpi;
$retina-dppx:                       1.5dppx;
